import { Component, H, ComponentProps, Pojo } from "../../Component";
import * as dojoDate from "dojo/date/locale";

export interface HeaderProps extends ComponentProps {
    /**
     * Unique key for the component.
     * @type {string}
     */
    key: string;

    /**
     * True if the item's details are currently being viewed.
     * @type {boolean}
     */
    active: boolean;

    /**
     * Mapping of CSS classes for the header.
     * @type {object}
     */
    classes: { [propName: string]: boolean; };

    /**
     * Handler for showing an item's details.
     * @type {function}
     */
    handleShowItemDetails: (e: Event) => void;

    /**
     * URL for the item's icon.
     * @type {string}
     */
    iconUrl: string;

    /**
     * URL for the user's profile.
     * @type {string}
     */
    userUrl: string;

    /**
     * The item for the card.
     * @type {object}
     */
    item: Pojo;

    /**
     * The portal that the item is associated with.
     * @type {object}
     */
    portal: Pojo;

    /**
     * The current sort field.
     * @type {string}
     */
    sortField: string;

    /**
     * The URI for the item's thumbnail.
     * @type {string}
     */
    thumbURI: string;

    /**
     * nls for the component.
     * @type {object}
     */
    i18n: Pojo;

    /**
     * Optional organization that the item is affiliated with.
     * @type {object}
     */
    organization?: Pojo;
}

export default class MiniCardHeader extends Component<HeaderProps> {
    public render(tsx: H) {
        const {
            active,
            classes,
            handleShowItemDetails,
            item,
            iconUrl,
            userUrl,
            portal,
            sortField,
            i18n
        } = this.props;

        let infoString: string;
        if (sortField === "numviews") {
            infoString = `${i18n.viewCount}: ${item.numViews}`;
        } else if (sortField === "avgrating") {
            infoString = `${i18n.rating}: ${item.avgRating.toFixed(2)}`;
        } else if (sortField === "created") {
            infoString = `${i18n.created}: ${
                dojoDate.format(new Date(item.created), { selector: "date", formatLength: "short" })
            }`;
        } else {
            infoString = `${i18n.updated}: ${
                dojoDate.format(new Date(item.modified), { selector: "date", formatLength: "short" })
            }`;
        }

        return (
            <header classes={classes}>
                <img
                    src="data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7"
                    alt=""
                    class="card-mc__thumbnail"
                    style={`
                        background-image: url(${this.props.thumbURI});
                    `}
                />
                <div class="card-mc__title-container">
                    <button
                        id={`item-${item.id}-card-title`}
                        class="card-mc__title"
                        title={active ? i18n.hideItem : i18n.viewItem}
                        tabindex="0"
                    >
                        {item.title}
                    </button>
                    <div class="card-mc__author-row">
                        <img
                            src={iconUrl}
                            class="content-search-item-icon"
                            title={item.displayName}
                        />
                        <span
                            class="card-mc__author-text"
                        >
                            {i18n.by}
                            <a
                                class="content-search-selectable card-mc__author-link"
                                title={this.props.organization ? i18n.viewOrg : i18n.viewProfile}
                                href={
                                    this.props.organization ?
                                    this.props.organization.orgUrl :
                                    userUrl
                                }
                                target="_blank"
                                onclick={stopPropagation}
                            >
                                {` ${this.props.organization ? this.props.organization.name : item.owner}`}
                            </a>
                        </span>
                    </div>
                    <p class="card-mc__info">{infoString}</p>
                </div>
            </header>
        );
    }
}

function stopPropagation(e: Event) {
    e.stopPropagation();
}
