import * as componentI18n from "dojo/i18n!../nls/resources";

import { Component, H, Pojo } from "../../Component";
import Header from "./Header";
import ActionRow from "./ActionRow";
import LoaderBars from "../../Loaders/LoaderBars";

// Badges
import AuthoritativeBadge from "../../Badges/Authoritative";
import DeprecatedBadge from "../../Badges/Deprecated";
import LivingAtlasBadge from "../../Badges/LivingAtlas";
import MarketplaceBadge from "../../Badges/Marketplace";
import OpenDataBadge from "../../Badges/OpenData";
import PremiumBadge from "../../Badges/Premium";
import SubscriberBadge from "../../Badges/Subscriber";

export interface CustomAction {
    allowed: (item: Pojo) => boolean;
    asynchronous: boolean;
    onAction: (item: Pojo) => dojo.Deferred<any>;
    name: string;
    icon?: string;
    constrained?: boolean;
}

export interface MiniCardProps {
    /**
     * A unique key for the component.
     * @type {string}
     */
    key: string;

    /**
     * The item for the card.
     * @type {object}
     */
    item: Pojo;

    /**
     * Mapping of item ids that are currently in the loading state.
     * @type {object}
     */
    loadingItems: { [itemId: string]: Pojo };

    /**
     * Mapping of item ids that are currently in the selected state.
     * @type {object}
     */
    selectedItems: { [itemId: string]: Pojo };

    /**
     * The current sort field.
     * @type {string}
     */
    sortField: string;

    /**
     * The portal currently being queried.
     * @type {object}
     */
    portal: Pojo;

    /**
     * Handler for viewing an item's details.
     * @type {function}
     */
    onShowItemDetails: (item: Pojo) => void;

    /**
     * Handler for selecting an item.
     * @type {function}
     */
    onSelectionClick: (item: Pojo) => void;

    /**
     * The item that is currently being viewed in the details panel.
     * @type {object}
     */
    activeItem: Pojo | undefined;

    /**
     * Array of custom actions for the item.
     * @type {array}
     */
    customActions: CustomAction[];

    /**
     * Handler for firing a custom action.
     * @type {function}
     */
    onCustomActionClick: (actionTitle: string, item: Pojo) => void;

    /**
     * If true, the main action will not be available for this item.
     * @type {boolean}
     */
    disableMainAction?: boolean;

    /**
     * If true, the information on the card will wrap below the thumbnail.
     * @type {boolean}
     */
    constrained?: boolean;

    /**
     * If true, no ellipses will be shown for custom actions.
     * @type {boolean}
     */
    hideEllipses?: boolean;

    /**
     * Optional organization that this item is affiliated with.
     * @type {object}
     */
    organization?: Pojo;
}

/**
 * An item card used in the `ItemBrowser`
 */
export default class MiniCard extends Component<MiniCardProps> {
    constructor(props: MiniCardProps) {
        super(props);
        
        this.handleShowItemDetails = this.handleShowItemDetails.bind(this);
        this.handleSelectionClick = this.handleSelectionClick.bind(this);
        this.handleCustomActionClick = this.handleCustomActionClick.bind(this);
        this.handleIconActionClick = this.handleIconActionClick.bind(this);
    }

    public render(tsx: H) {
        const selected = !!this.props.selectedItems[this.props.item.id];
        const loading = !!this.props.loadingItems[this.props.item.id];
        const active = !!(this.props.activeItem && this.props.activeItem.id === this.props.item.id);

        const availableActions = this.props.customActions.filter((action: CustomAction) => {
            return action.allowed(this.props.item);
        }).map((action: CustomAction) => ({ value: action.name, text: action.name, icon: action.icon }));

        const availableIconActions = availableActions
            .filter((action: { value: string, text: string, icon?: string }) => {
                return !!action.icon;
            });

        const availableMiscActions = availableActions
            .filter((action: { value: string, text: string, icon?: string }) => {
                return !action.icon;
            });

        const containerClasses = {
            "card-mc__container": true,
            "card-mc__container--selected": selected,
            "card-mc__container--active": active
        };
        
        const faderClasses = {
            "card-mc__fader": true,
            "card-mc__fader--loading": loading
        };

        const headerClasses = {
            "card-mc__header": true,
            "card-mc__header--constrained": !!this.props.constrained
        };

        return (
            <section
                key={this.props.item.id}
                classes={containerClasses}
            >
                <div classes={faderClasses} onclick={this.handleShowItemDetails}>
                    <Header
                        active={active}
                        classes={headerClasses}
                        handleShowItemDetails={this.handleShowItemDetails}
                        i18n={componentI18n}
                        iconUrl={this.props.item.iconURI}
                        item={this.props.item}
                        key="mini-card-header"
                        portal={this.props.portal}
                        sortField={this.props.sortField}
                        thumbURI={this.props.item.thumbURI}
                        userUrl={`${this.props.portal.baseUrl}/home/user.html?user=${this.props.item.owner}`}
                        organization={this.props.organization}
                    />
                    <ActionRow
                        selected={selected}
                        handleSelectionClick={this.handleSelectionClick}
                        loading={loading}
                        id={this.props.item.id}
                        i18n={componentI18n}
                        availableMiscActions={availableMiscActions}
                        availableIconActions={availableIconActions}
                        handleIconActionClick={this.handleIconActionClick}
                        handleCustomActionClick={this.handleCustomActionClick}
                        disableMainAction={this.props.disableMainAction}
                        hideEllipses={this.props.hideEllipses}
                    >
                        <div key="badges">
                            {this.renderBadges(tsx)}
                        </div>
                    </ActionRow>
                </div>
                {
                    loading ? 
                    <LoaderBars key="item-card-loader-bars" /> : null
                }
            </section>
        );
    }

    private renderBadges(tsx: H) {
        return this.props.item.badges.map((badgeType: string) => {
            switch (badgeType) {
                case "authoritative":
                    return (
                        <AuthoritativeBadge
                            size="small"
                            altOrg={this.props.organization ? this.props.organization.name : undefined}
                        />
                    );
                case "deprecated":
                    return <DeprecatedBadge size="small" />;
                case "livingAtlas":
                    return <LivingAtlasBadge size="small" />;
                case "marketplace":
                    return <MarketplaceBadge size="small" />;
                case "openData":
                    return <OpenDataBadge size="small" />;
                case "premium":
                    return <PremiumBadge size="small" user={this.props.portal.user} />;
                case "subscriber":
                    return <SubscriberBadge size="small" user={this.props.portal.user} />;
                default: //
            }
            return null;
        });
    }

    private handleShowItemDetails(e: Event) {
        this.props.onShowItemDetails(this.props.item);
    }

    private handleSelectionClick(e: any) {
        e.stopPropagation();
        this.props.onSelectionClick(this.props.item);
    }

    private handleIconActionClick(e: any) {
        e.stopPropagation();
        if (e.target.value) {
            this.props.onCustomActionClick(e.target.value, this.props.item);
        }
    }
    
    private handleCustomActionClick(actionTitle: string) {
        this.props.onCustomActionClick(actionTitle, this.props.item);
    }
}
