import { H, SFCProps, Pojo } from "../../Component";
import TextOptionDropdown from "../../Dropdowns/TextOptionDropdown";
import SvgButton from "../../Buttons/SvgButton";
import LoaderIcon from "../../Loaders/LoaderIcon";

export interface ActionRowProps extends SFCProps {
    /**
     * The selection state of the item.
     * @type {boolean}
     */
    selected: boolean;

    /**
     * Handler for selecting the item.
     * @type {function}
     */
    handleSelectionClick: (e: any) => void;

    /**
     * Handler for firing an icon action.
     * @type {function}
     */
    handleIconActionClick: (e: any) => void;

    /**
     * Handler for firing a custom action.
     * @type {function}
     */
    handleCustomActionClick: (e: any) => void;

    /**
     * Array of available custom actions on the card.
     * @type {array}
     */
    availableMiscActions: { value: string, text: string }[];

    /**
     * Array of available icon actions on the card.
     * @type {array}
     */
    availableIconActions: { value: string, text: string, icon?: string }[];

    /**
     * Loading state of the item.
     * @type {boolean}
     */
    loading: boolean;

    /**
     * If true, the main action will not be available for this item.
     * @type {boolean}
     */
    disableMainAction?: boolean;

    /**
     * If true, no ellipses will be shown for custom actions.
     * @type {boolean}
     */
    hideEllipses?: boolean

    /**
     * The item's id.
     * @type {string}
     */
    id: string;

    /**
     * nls for the card.
     */
    i18n: Pojo;
}

export default ({
    selected,
    handleSelectionClick,
    handleIconActionClick,
    handleCustomActionClick,
    availableMiscActions,
    availableIconActions,
    loading,
    disableMainAction,
    children,
    id,
    hideEllipses,
    i18n
}: ActionRowProps, tsx: H) => {

    let selectBtn = null;
    if (!disableMainAction) {
        if (loading) {
            selectBtn = <LoaderIcon />;
        } else {
            selectBtn = (
                <SvgButton
                    focusable={true}
                    handleClick={handleSelectionClick}
                    key={`${id}-selection-btn`}
                    title={selected ? i18n.remove : i18n.add}
                >
                    {
                        selected ? (
                            <svg
                                width="16px"
                                height="16px"
                                viewBox="0 0 16 16"
                            >
                                <g id="16px" stroke="none" stroke-width="1">
                                    <g>
                                        <g>
                                            <polygon points="3 7 12 7 12 8 3 8" />
                                            <path
                                                d="M7.501,0 C3.365,0 0,3.364 0,7.5 C0,11.636 3.365,15 7.501,15 C11.636,15 15,11.636 15,7.5 C15,3.364 11.636,0 7.501,0 Z M7.501,14 C3.916,14 1,11.084 1,7.5 C1,3.916 3.916,1 7.501,1 C11.085,1 14,3.916 14,7.5 C14,11.084 11.085,14 7.501,14 Z"
                                            />
                                        </g>
                                    </g>
                                </g>
                            </svg>
                        ) : (
                            <svg
                                width="16px"
                                height="16px"
                                viewBox="0 0 16 16"
                            >
                                <g id="16px" stroke="none" stroke-width="1">
                                    <g>
                                        <g>
                                            <polygon
                                                points="12 7 7.999 7 8 3 7.001 3 7 7 2.999 7 2.999 8 7 8 6.999 12 7.998 12 7.999 8 12 8"
                                            />
                                            <path
                                                d="M7.501,0 C3.365,0 0,3.364 0,7.5 C0,11.636 3.365,15 7.501,15 C11.636,15 15,11.636 15,7.5 C15,3.364 11.636,0 7.501,0 Z M7.501,14 C3.916,14 1,11.084 1,7.5 C1,3.916 3.916,1 7.501,1 C11.085,1 14,3.916 14,7.5 C14,11.084 11.085,14 7.501,14 Z"
                                            />
                                        </g>
                                    </g>
                                </g>
                            </svg>
                        )
                    }
                </SvgButton>
            );
        }
    }

    const iconActionBtns = availableIconActions.map((action: { value: string, text: string, icon: string }) => (
        <SvgButton
            focusable={true}
            handleClick={handleIconActionClick}
            key={`${id}-${action.text}`}
            title={action.text}
            value={action.value}
        >
            <div class="card-mc__custom-icon-container" innerHTML={action.icon} />
        </SvgButton>
    ));

    return (
        <div id={`item-${id}-action-row`} class="content-search-item-action-row card-mc__action-row">
            {children}
            <div key="action-container" class="card-mc__action-container">
                <SvgButton key={`${id}-spacer`}><svg height="16px" width="0px" /></SvgButton>
                {selectBtn}
                {iconActionBtns}
                {
                    availableMiscActions.length > 0 && !hideEllipses ?
                    <TextOptionDropdown
                        key={`${id}-actions`}
                        options={availableMiscActions}
                        onSelect={handleCustomActionClick}
                        selectedOptionValue={undefined}
                        justify="right"
                    >
                            <SvgButton
                                focusable={false}
                                key={`${id}-action-btn`}
                                title={i18n.actions}
                            >
                                <svg
                                    width="16px"
                                    height="16px"
                                    style="transform: translate(0, -2px);"
                                >
                                    <g stroke="none" stroke-width="1">
                                        <g>
                                            <path
                                                d="M1,7 L3,7 L3,9 L1,9 L1,7 Z M7,7 L9,7 L9,9 L7,9 L7,7 Z M13,7 L15,7 L15,9 L13,9 L13,7 Z"
                                            />
                                        </g>
                                    </g>
                                </svg>
                            </SvgButton>
                    </TextOptionDropdown> : null
                }
            </div>
        </div>
    );
};
